@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";


* {
    font-family: 'Montserrat';
    font-weight: 400;
    font-style: normal;
    color: $text-main-color;
    text-decoration: none;
}

.registration {
    background: #f5f1ee;
    height: 100vh;
    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }
}

.registration-from {
    max-width: 480px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;

    background: #f5f1ee;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}
.registration-from__header {
    display: block;
    background: #ede4de;
    padding: 27px 40px 20px 40px;
    
    h3 {
        font-size: 18px;
        line-height: 22px;
        color: #6a3f24;
        position: relative;
        padding-bottom: 10px;
        border-bottom: 1px solid #00b810;
        position: relative; 
        
        &::after {
            content: '';
            height: 3px;
            width: 58%;
            display: block;
            background: #00b810;
            position: absolute;
            bottom: 0;
        }
    }
}
.registration-from__wrapper {
    padding: 20px 40px 40px;

}



.myPopup__form-input, .registration-from__textarea {
    width: 100%;
    padding: 17px 0 0 0;
    border: none;
      border-bottom-color: currentcolor;
      border-bottom-style: none;
      border-bottom-width: medium;
    border-bottom: 1px solid RGBA(106,63,36,0.4);
    min-height: 40px;
    margin: 0 auto;
    background: inherit;
    font-size: 14px;
    line-height: 20px;
    color: #6a3f24;
  }
  .myPopup__form-button {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.0357143em;
    text-transform: uppercase;
    color: #ffffff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 240px;
    height: 40px;
    background: #00b810;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.12), 0px 8px 8px rgba(0, 0, 0, 0.24);
    border-radius: 2px;
    margin-top: 40px;
    border: none;
    cursor: pointer;
  }